:root {
  --active-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__nav {
  margin-right: 50px;
}

.nav {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  list-style: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.header__logo {
  margin-left: 50px;
  display: flex;
}

.header__logo-image {
  width: 40px;
  height: 40px;
}

